.contents{
  
 &-area{
   width: 100%;
   border-top: 1px black solid;
  &:nth-of-type(4){
    border-bottom: 1px black solid;
  }
 }
 &-item{
   padding: 20px 0;
   display: flex;
   flex-wrap: wrap;
   width: 100%;
 }
 &-img{
   width: 25%;
   @include mq{
     width: 100%;
   }
   img{
     width: 100%;
     @include mq{
      width: 100%;
    }
   }
 }
 &-textarea{
   width: 75%;
   padding-left: 3%;
   
  @include mq{
    padding-left: 0;
    padding-top: 10px;
    width: 100%;
  }
 }
 &-heading{
   @include fz(24);
 }
 &-text{
   @include fz(16);
 }
}